import React,{useState} from 'react'
import dayJs from 'dayjs'
import { Input } from 'antd';

const TomatoItem =  (props)=>{
  const [isEdit,toggleEdit] = useState(false)
  const [description,changeDesc] = useState(props.description ||'')
  const handleChange = (e:React.ChangeEvent<HTMLInputElement>)=>{
    if(e.target.value !==description){
      changeDesc(e.target.value)
    }
  }
  const updateTomatoes = ()=>{
    props.AbortTomatoes(props.id,{description})
    toggleEdit(false)
  }
  const deleteTomatoes = ()=>{
    props.AbortTomatoes(props.id,{aborted:true,description:props.description||'aborted tomatoes'})
    toggleEdit(false)
  }
  const cancelUpdate = ()=>{
    changeDesc(props.description||'')
    toggleEdit(false)
  }
  return  (
    <li className='item' key={props.id}>
      {props.aborted ? <span>{dayJs(props.started_at).format('YYYYMMDD')}</span>:null}
      <span className='date_time'>{dayJs(props.started_at).format('hh:mm')}-{dayJs(props.ended_at).format('hh:mm')}</span>
      {
        isEdit ?
        <Input value={description} onChange={handleChange} onPressEnter={updateTomatoes}/>:
          <span className='description'>{description} <p className={props.manually_created ? 'active':''}></p>  </span>
      }
      {
        !isEdit ? (
          <div className='action_wrapper'>
            <span onClick={()=>toggleEdit(true)}></span>
            <span onClick={deleteTomatoes}></span>
          </div>
        ):(
          <div className='action_wrapper'>
            <span onClick={updateTomatoes}></span>
            <span onClick={cancelUpdate}></span>
          </div>
        )
      }
    </li>
  )
}

export default TomatoItem



